@font-face {
  font-family: "Roboto Condensed";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Roboto Condensed"), url("../../fonts/roboto-condensed/robotocondensed-regular.woff") format("woff2"), url("../../fonts/roboto-condensed/robotocondensed-regular.woff") format("woff");
}
@font-face {
  font-family: "Roboto Condensed";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local("Roboto Condensed"), url("../../fonts/roboto-condensed/robotocondensed-bold.woff") format("woff2"), url("../../fonts/roboto-condensed/robotocondensed-bold.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local("Roboto"), url("../../fonts/roboto/roboto-medium.woff") format("woff2"), url("../../fonts/roboto/roboto-medium.woff") format("woff");
}
@font-face {
  font-family: "PT Mono";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local("PT Mono"), url("../../fonts/ptmono/ptmono-regular.woff") format("woff2"), url("../../fonts/ptmono/ptmono-regular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "PT Mono", "Arial", sans-serif;
  color: #041827;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #e5e5e5;
}

a {
  color: #e5e5e5;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #e5e5e5;
  -webkit-text-fill-color: #041827;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 30px 410px 35px 350px;
}
@media (max-width: 1919px) {
  .container {
    padding: 20px 70px 25px 50px;
  }
}
@media (max-width: 769px) {
  .container {
    padding: 10px 35px 20px 26px;
  }
}
.container--footer {
  padding: 50px 350px 55px;
}
@media (max-width: 1919px) {
  .container--footer {
    padding: 50px 45px 65px 50px;
  }
}
@media (max-width: 769px) {
  .container--footer {
    padding: 35px 0;
  }
}

.page-container {
  display: flex;
  flex-direction: column;
  background-color: #f8fdff;
}

.button {
  display: inline-flex;
  color: #e5e5e5;
  text-transform: uppercase;
  background-color: #00b2ff;
  border: none;
  cursor: pointer;
}
.button--transparent {
  background-color: transparent;
}
.button:disabled {
  opacity: 0.5;
  pointer-events: none;
}
.button:hover {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(4, 24, 39, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 769px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #e5e5e5;
}
@media (max-width: 769px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: transparent;
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 769px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.header {
  position: sticky;
  top: 0;
  z-index: 2;
  display: flex;
  flex-wrap: wrap;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
}

.header__contacts-list {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
  gap: 30px;
  list-style: none;
}
@media (max-width: 1919px) {
  .header__contacts-list {
    margin-left: auto;
    gap: 20px;
  }
}
@media (max-width: 769px) {
  .header__contacts-list {
    align-items: center;
  }
}

.header__contacts-item {
  width: 225px;
  line-height: 18px;
}
@media (max-width: 1919px) {
  .header__contacts-item {
    width: 40px;
  }
}

.header__contact-link {
  display: flex;
  width: 100%;
  gap: 15px;
  padding: 15px 0;
  align-items: center;
}
@media (max-width: 1919px) {
  .header__contact-link {
    padding: 10px;
    justify-content: center;
  }
  .header__contact-link span {
    display: none;
  }
}

.header__order-call-button {
  width: 160px;
  height: 40px;
  padding: 10px 15px;
  margin-left: auto;
  font-size: 14px;
  line-height: 16px;
  border: 1px #e5e5e5 solid;
  border-radius: 3px;
  align-self: center;
  text-align: center;
}
@media (max-width: 1919px) {
  .header__order-call-button {
    display: none;
  }
}

.main-logo {
  display: flex;
  flex-wrap: wrap;
  width: 215px;
  margin-right: 160px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 25px;
  line-height: 29px;
  font-weight: 700;
  align-items: center;
}
.main-logo--footer {
  margin-bottom: 30px;
}
@media (max-width: 1919px) {
  .main-logo--footer {
    margin: 0;
  }
}
@media (max-width: 769px) {
  .main-logo--header {
    width: min-content;
    align-self: flex-end;
    margin: 0;
    height: 35px;
  }
  .main-logo--header .main-logo__logo {
    width: 35px;
    height: 35px;
    margin: 0;
  }
  .main-logo--header .main-logo__logo-tagline {
    display: none;
  }
}

.main-logo__logo {
  width: 55px;
  height: 55px;
  margin-right: 15px;
}

.main-logo__logo-tagline {
  display: flex;
  flex-direction: column;
}

.main-logo p {
  margin: 0 0 5px 0;
}

.main-logo span {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 12px;
  line-height: 14px;
  font-weight: 500;
  color: rgba(229, 229, 229, 0.7);
}

.main-info {
  position: relative;
  top: -18px;
  color: #f2f5f6;
  background-image: url("../../img/main-background-desktop.jpg");
  background-position: bottom left;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .main-info {
    background-image: url("../../img/main-background-desktop@2x.jpg");
  }
}
@media (max-width: 1919px) {
  .main-info {
    background-image: url("../../img/main-background-tablet.jpg");
    background-position: bottom 0 left -123px;
  }
}
@media (max-width: 1919px) and (min-resolution: 144dpi), (max-width: 1919px) and (min-resolution: 1.5dppx) {
  .main-info {
    background-image: url("../../img/main-background-tablet@2x.jpg");
  }
}
@media (max-width: 769px) {
  .main-info {
    background-image: url("../../img/main-background-mobile.jpg");
    background-position: bottom 0 left -205px;
  }
}
@media (max-width: 769px) and (min-resolution: 144dpi), (max-width: 769px) and (min-resolution: 1.5dppx) {
  .main-info {
    background-image: url("../../img/main-background-mobile@2x.jpg");
  }
}

.main-info__wrapper {
  display: flex;
  flex-direction: column;
  padding: 185px 475px 280px;
  background: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%);
}
@media (max-width: 1919px) {
  .main-info__wrapper {
    padding: 80px 50px 205px;
  }
}
@media (max-width: 769px) {
  .main-info__wrapper {
    padding: 80px 25px 190px;
  }
}

.main-info__title {
  margin: 0 0 40px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 80px;
  line-height: 94px;
  text-transform: uppercase;
}
@media (max-width: 1919px) {
  .main-info__title {
    margin-bottom: 20px;
    font-size: 64px;
    line-height: 75px;
    letter-spacing: 0.07em;
  }
}
@media (max-width: 769px) {
  .main-info__title {
    font-size: 36px;
    line-height: 42px;
  }
}

.main-info__text-wrapper {
  width: 685px;
  margin-bottom: 40px;
  font-size: 22px;
  line-height: 34px;
}
@media (max-width: 769px) {
  .main-info__text-wrapper {
    width: 100%;
    margin-bottom: 50px;
    font-size: 14px;
    line-height: 21px;
  }
}

.main-info__text-wrapper p {
  margin: 0;
}

.main-info__consultation-button {
  display: flex;
  max-width: 410px;
  padding: 15px 25px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  background-color: #00b2ff;
  border-radius: 3px;
  justify-content: center;
}
@media (max-width: 769px) {
  .main-info__consultation-button {
    font-size: 14px;
    line-height: 16px;
  }
}

.main-info__consultation-button .vp-max-769 {
  display: none;
}
@media (max-width: 769px) {
  .main-info__consultation-button span {
    display: none;
  }
  .main-info__consultation-button .vp-max-769 {
    display: inline;
  }
}

.footer {
  display: flex;
  flex-wrap: wrap;
  line-height: 20px;
  background-color: #041827;
}
@media (max-width: 1919px) {
  .footer {
    gap: 40px 130px;
  }
}
@media (max-width: 769px) {
  .footer {
    gap: 0;
  }
}

.footer__social-wrapper {
  display: flex;
  flex-direction: column;
}
@media (max-width: 1919px) {
  .footer__social-wrapper {
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
    align-items: center;
  }
}
@media (max-width: 769px) {
  .footer__social-wrapper {
